import {
	mapState
} from 'vuex'
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/empty/empty";
export default {
	components: {
		uniLoadMore,
		empty
	},
	data() {
		return {
			listData: [],
			searchData: {
				orderState: null,
				accountId: '',
				pageNum: 1,
				query: ''
			},
			isPull: false
		};
	},
	
	onLoad(options){
		console.log(options)
		/**
		 * 修复app端点击除全部订单外的按钮进入时不加载数据的问题
		 * 替换onLoad下代码即可
		 */
		if(options.state == 0){
			this.isPull=true;
		}
		this.tabCurrentIndex = +options.state;
		
		this.loadData()
		if(options.state == 0){
			this.loadData()
		}
		
	},
	onReachBottom: function(){
		if(!this.isPull) return;
		console.log(111);
	},
	methods: {
		transOrderState(num){
			switch(num){
				case 0:
					return '未支付';
					break;
				case 1:
					return '未发货';
					break;
				case 2:
					return '已发货';
					break;
				case 3:
					return '已完成';
					break;
				case 4:
					return '已退货';
					break;
				case 5:
					return '已退款';
					break;
				case 6:
					return '已取消';
					break;
				case 7:
					return '退款中';
					break;
				case 9:
					return '备货中';
					break;
				default:
					return '';
			}
		},
		//获取订单列表
		loadData(source){
			console.log(source)
			this.$http.post('/mall/app/order/list', {
				...this.baseInfo,
				...this.searchData,
				accountId: this.userInfo.accountId
			})
			.then( res => {
				if(res.code == 0){
					if(res.result){
						// this.listData=res.result.records;
						// this.listData.map(item => {
						// 	this.$set(item, 'orderStateName', this.transOrderState(item.orderlistState));
						// });
						// console.log(this.listData)
						if(this.searchData.pageNum == 1){
							if(res.result.records){
								this.listData=res.result.records;
							}
						}else{
							this.listData=this.listData.concat(res.result.records);
						}
					}
				}
			})
			.catch( err => {
				console.log(err);
			})
			//这里是将订单挂载到tab列表下
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let state = navItem.state;
			
			// if(source === 'tabChange' && navItem.loaded === true){
			// 	//tab切换只有第一次需要加载数据
			// 	return;
			// }
			// if(navItem.loadingType === 'loading'){
			// 	//防止重复加载
			// 	return;
			// }
			// 
			// navItem.loadingType = 'loading';
			// 
			setTimeout(()=>{
				let orderList = this.listData.filter(item=>{
					//添加不同状态下订单的表现形式
					item = Object.assign(item, this.orderStateExp(item.state));
					//演示数据所以自己进行状态筛选
					if(state === 0){
						//0为全部订单
						return item;
					}
					return item.state === state
				});
				orderList.forEach(item=>{
					navItem.orderList.push(item);
				})
				// // loaded新字段用于表示数据加载完毕，如果为空可以显示空白页
				// this.$set(navItem, 'loaded', true);
				// 
				// //判断是否还有数据， 有改为 more， 没有改为noMore 
				// navItem.loadingType = 'more';
			}, 600);
		}, 
		
		//swiper 切换
		changeTab(e){
			if(e.target.current == 0){
				this.isPull=true;
			}else{
				this.isPull=false;
			}
			this.tabCurrentIndex = e.target.current;
			this.loadData('tabChange');
		},
		//顶部tab点击
		tabClick(index){
			this.tabCurrentIndex = index;
		},
		// 跳转详情
		linkToDetail(orderId){
			uni.navigateTo({
				url: '/pages/orderDetail/orderDetail'
			})
		},
		//取消订单
		cancelOrder(item){
			uni.showModal({
			    title: '提示',
			    content: '确定取消订单吗？',
				confirmColor: '#E93B3D',
			    success: function (res) {
			        if (res.confirm) {
			            console.log('用户点击确定');
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
			// uni.showLoading({
			// 	title: '请稍后'
			// })
			// setTimeout(()=>{
			// 	let {stateTip, stateTipColor} = this.orderStateExp(9);
			// 	item = Object.assign(item, {
			// 		state: 9,
			// 		stateTip, 
			// 		stateTipColor
			// 	})
			// 	
			// 	//取消订单后删除待付款中该项
			// 	let list = this.navList[1].orderList;
			// 	let index = list.findIndex(val=>val.id === item.id);
			// 	index !== -1 && list.splice(index, 1);
			// 	
			// 	uni.hideLoading();
			// }, 600)
		},
		// 去付款
		linkToPayment(){
			uni.navigateTo({
				url: '/pages/payment/payment'
			})
		},
		// 查看物流详情
		linkToDelivery(){
			uni.navigateTo({
				url: '/pages/delivery/delivery'
			})
		},
		// 确认收货
		confirmOrder(){
			console.log("确认收货");
		},
		//删除订单
		deleteOrder(index){
			// uni.showLoading({
			// 	title: '请稍后'
			// })
			// setTimeout(()=>{
			// 	this.navList[this.tabCurrentIndex].orderList.splice(index, 1);
			// 	uni.hideLoading();
			// }, 600)
		},
		

		//订单状态文字和颜色
		orderStateExp(state){
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch(+state){
				case 1:
					stateTip = '待付款'; break;
				case 2:
					stateTip = '待收货'; break;
				case 3:
					stateTip = '已完成'; break;
				case 4:
					stateTip = '售后'; break;
				case 9:
					stateTip = '订单已关闭'; 
					stateTipColor = '#909399';
					break;
					
				//更多自定义
			}
			return {stateTip, stateTipColor};
		}
	},
	computed: {
		...mapState('common', ['baseInfo', 'userInfo'])
	}
}
